import React, { Component } from 'react'
// import "../newsItem.css"

export class NewsItem extends Component {
	render() {
		let imgUrl = this.props.urlToImage === null ? "#" : this.props.urlToImage;
		let title = this.props.title === null ? "No Title" : this.props.title;
		let description = this.props.description === null ? "No Discreption Available" : this.props.description;
		let url = this.props.url === null ? "#" : this.props.url;
		let publishedAt = this.props.publishedAt === null ? "" : new Date(this.props.publishedAt).toString().slice(0, 15);
		let author = this.props.author === null ? "Unknown" : this.props.author;
		return (
			<a href={url} rel="noreferrer" target="_blank" className="card mx-3 my-2" title="Click to read more">
				<img src={imgUrl} className="card-img-top" alt="No Preview Available" />
				<div className="card-body">
					<h5 className="card-title">{title}</h5>
					<p className="card-text">{description}</p>
					<p className="publishedAt card-text"><small>By {author} on {publishedAt}</small></p>
					{/* <a href={url} rel="noreferrer" target="_blank" className="btn btn-sm btn-dark">Read More</a> */}
				</div>
			</a>
		)
	}
}

export default NewsItem
