import React, { Component } from 'react'
import NewsItem from './NewsItem'
import Spinner from './Spinner';
// import dataJson from '../smapleOutput.json'


export class News extends Component {

	constructor() {
		super();
		this.state = {
			news: [],
			loading: false,
			pageNumber: 1,
			totalPage: 1,
			pageSize: 18
		}
	}

	async componentDidMount() {
		const fetchUrl = `https://newsapi.org/v2/top-headlines?country=in&category=${this.props.category}&pageSize=${this.state.pageSize}&apiKey=${this.props.apikey}&page=${this.state.pageNumber}`;
		this.setState({ loading: true })
		const data = await (await fetch(fetchUrl)).json();
		this.setState({
			news: data.articles,
			pageNumber: 1,
			totalPage: Math.ceil(data.totalResults / this.state.pageSize),
			loading: false
		});
	}

	next = async () => {
		const fetchUrl = `https://newsapi.org/v2/top-headlines?country=in&category=${this.props.category}&pageSize=${this.state.pageSize}&apiKey=${this.props.apikey}&page=${this.state.pageNumber + 1}`;
		this.setState({ loading: true })
		const data = await (await fetch(fetchUrl)).json();
		this.setState({
			news: data.articles,
			pageNumber: (this.state.pageNumber + 1),
			loading: false
		})
	}
	prev = async () => {
		const fetchUrl = `https://newsapi.org/v2/top-headlines?country=in&category=${this.props.category}&pageSize=${this.state.pageSize}&apiKey=${this.props.apikey}&page=${this.state.pageNumber - 1}`;
		this.setState({ loading: true })
		const data = await (await fetch(fetchUrl)).json();
		this.setState({
			news: data.articles,
			pageNumber: (this.state.pageNumber - 1),
			loading: false
		})
	}

	render() {
		return (
			<div className="container my-3">
				<h1 className="my-3 text-center">Top Headlines - {this.props.category.charAt(0).toUpperCase() + this.props.category.slice(1)}</h1>
				<div className="d-flex justify-content-between">
					<button disabled={this.state.pageNumber <= 1} onClick={this.prev} type="button" className="btn btn-dark">← Previous</button>
					<button disabled={this.state.pageNumber >= this.state.totalPage} onClick={this.next} type="button" className="btn btn-dark">Next →</button>
				</div>
				{this.state.loading && <Spinner />}
				<div className="d-flex flex-row justify-content-center align-items-center flex-wrap my-3">
					{
						!this.state.loading &&
						this.state.news.map(news => {
							return (
								<NewsItem key={news.url} title={news.title} description={news.description} publishedAt={news.publishedAt} url={news.url} urlToImage={news.urlToImage} author={news.author} />
							)
						})
					}
				</div>
				<div className="d-flex justify-content-between">
					<button disabled={this.state.pageNumber <= 1} onClick={this.prev} type="button" className="btn btn-dark">← Previous</button>
					<button disabled={this.state.pageNumber >= this.state.totalPage} onClick={this.next} type="button" className="btn btn-dark">Next →</button>
				</div>
			</div>
		)
	}
}

export default News
